import React, { useState } from 'react';
import logo from '../Assets/header_logo.svg';
import './Header.scss';
import Container from "../Grid/Container";
import Row from "../Grid/Row";
import Col from "../Grid/Col";

function Header() {
    const [isMenuOpen, setIsMenuOpen] = useState(false);

    let scrollToTarget = (e, target) => {
        e.preventDefault();
        let targetElement = document.querySelector(target);
        if(!targetElement) return;
        let headerHeight = document.querySelector('.header').getBoundingClientRect().height;
        let elementPosition = targetElement.offsetTop;
        let offsetPosition = elementPosition - headerHeight;

        window.scrollTo({
            top: offsetPosition,
            behavior: "smooth"
        });
        setIsMenuOpen(false);
    };

    return (
        <header className={`header`}>
            <Container>
                <Row>
                    <Col size={6} pull={`left`} contentJustify={`center`}>
                        <img src={logo} className={`header__logo`} alt={`logo`} />
                    </Col>
                    <Col size={6} pull={`right`} contentJustify={`center`}>
                        <div className={`header__menu`}>
                            <button className="menu-toggle" onClick={() => setIsMenuOpen(!isMenuOpen)}>☰</button>
                            <ul className={isMenuOpen ? 'open' : ''}>
                                <li className={`active`}><button className={`btn-link`} onClick={(e) => { scrollToTarget(e, '#home'); }}>Home</button></li>
                                <li><button className={`btn-link`} onClick={(e) => { scrollToTarget(e, '#about-us'); }}>About us</button></li>
                                <li><button className={`btn-link`} onClick={(e) => { scrollToTarget(e, '#contact-us'); }}>Contact</button></li>
                            </ul>
                        </div>
                    </Col>
                </Row>
            </Container>
        </header>
    );
}

export default Header;
